<template>
  <!-- Search query field. -->
  <div class="max-w-400">
    <v-text-field
      dense
      clearable
      hide-details
      class="shrink"
      v-model="updateSearchQuery"
      :placeholder="$t('message.3')"
      append-icon="mdi-magnify"
      @input="getDocumentList()"
    >
      <template v-slot:append-outer>
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <!-- Paste search data from clipboard. -->
            <v-btn
              fab
              small
              depressed
              color="btnPrimary"
              v-bind="attrs"
              v-on="on"
              @click="fromClipboard()"
              style="margin-top: -10px"
            >
              <v-icon color="white">mdi-content-paste</v-icon>
            </v-btn>
          </template>
          <span>{{ $t("message.71") }}</span>
        </v-tooltip>
      </template>
    </v-text-field>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";

export default {
  name: "SearchQuery",

  props: {
    getDocumentList: {
      type: Function,
      required: true,
    },
  },

  data: () => ({}),

  computed: {
    ...mapState("documentList", ["searchQuery"]),
    updateSearchQuery: {
      get: function () {
        return this.searchQuery;
      },
      set: function (text) {
        this.setSearchQuery(text ? text.trim() : text);
      },
    },
  },

  methods: {
    ...mapMutations("documentList", ["setSearchQuery"]),
    ...mapActions("popUpMsg", ["runShowMsg"]),
    // Data from the clipboard, paste into the search field.
    fromClipboard() {
      this.updateSearchQuery = "";
      if (navigator.clipboard) {
        navigator.clipboard
          .readText()
          .then((clipText) => {
            this.updateSearchQuery = clipText;
            this.$props.getDocumentList();
          })
          .catch((err) => {
            this.runShowMsg({ text: err, status: "error" });
          });
      } else {
        // Your browser does not support Clipboard.
        this.runShowMsg({
          text: `${this.$t("message.12")}<br/>${this.$t("message.63")}`,
          status: "error",
        });
      }
    },
  },
};
</script>
