<template>
  <!-- Filter-2 for selective fields. -->
  <div>
    <v-btn
      small
      rounded
      depressed
      color="btnPrimary"
      class="white--text"
      :disabled="dataFilter_2.length === 0"
      @click="showFilterDoc = true"
    >
      <v-icon color="white" left>mdi-filter</v-icon>
      <span>{{ $t("message.64") }}</span>
    </v-btn>

    <!-- Filter by categories, for selection type fields. -->
    <v-dialog persistent v-model="showFilterDoc" max-width="500px">
      <v-card>
        <v-card-actions class="pr-3 pt-2 pb-0">
          <v-spacer></v-spacer>
          <!-- Button - Close. -->
          <v-btn icon small @click="showFilterDoc = false">
            <v-icon color="error">mdi-close</v-icon>
          </v-btn>
        </v-card-actions>
        <v-card-title class="pt-0 pb-6">{{ $t("message.65") }}</v-card-title>
        <v-card-text>
          <v-row justify="center">
            <v-col
              cols="12"
              v-for="(filter_2, idxFilter) in dataFilter_2"
              :key="`filter-2-${idxFilter}`"
              class="mb-4"
            >
              <v-autocomplete
                dense
                :chips="filter_2.multiple"
                :deletable-chips="filter_2.multiple"
                :small-chips="filter_2.multiple"
                clearable
                hide-details
                item-text="title"
                item-value="value"
                class="shrink"
                v-model="selectedDataFilter_2[filter_2.field]"
                :label="filter_2.label"
                :multiple="filter_2.multiple"
                :items="filter_2.items"
                @input="getDocumentList()"
              >
                <template v-slot:prepend>
                  <v-tooltip top>
                    <template v-slot:activator="{ on }">
                      <v-btn
                        fab
                        x-small
                        depressed
                        :color="filter_2.negation ? 'btnPrimary' : 'btnRed'"
                        @click="
                          [
                            (filter_2.negation = !filter_2.negation),
                            getDocumentList(),
                          ]
                        "
                      >
                        <v-icon color="white" v-on="on">{{
                          filter_2.negation ? "mdi-minus" : "mdi-plus"
                        }}</v-icon>
                      </v-btn>
                    </template>
                    {{ $t("message.69") }}
                  </v-tooltip>
                </template>
              </v-autocomplete>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "FilterTwo",

  props: {
    getDocumentList: {
      type: Function,
      required: true,
    },
  },

  data: () => ({
    showFilterDoc: false,
  }),

  computed: {
    ...mapState("documentList", ["dataFilter_2", "selectedDataFilter_2"]),
  },
};
</script>
