<template>
  <!-- Document table. -->
  <v-simple-table fixed-header class="mt-4">
    <template v-slot:default>
      <thead
        class="document-list"
        :style="
          $vuetify.theme.dark ? 'background: #23262D;' : 'background: #f5f6fb;'
        "
      >
        <tr>
          <!-- Symbol - № -->
          <th align="left" style="text-align: left !important">&#8470;</th>
          <!-- Mark all documents for deletion. -->
          <th align="center">
            <v-checkbox
              hide-details
              v-model="updateDeleteAllDocsFlag"
              color="error"
              class="mt-0 pt-0"
              @change="markAllDocsForDeletion()"
              :disabled="documents.length === 0"
            ></v-checkbox>
          </th>
          <!-- Other headers. -->
          <th
            align="center"
            v-for="(field, idxHeader) in fields"
            :key="`header-${idxHeader}`"
          >
            {{ field.title }}
          </th>
          <th align="center">{{ $t("message.29") }}</th>
          <th align="center">{{ $t("message.30") }}</th>
          <th align="center">ID</th>
        </tr>
      </thead>
      <tbody class="document-list">
        <!-- Document list. -->
        <tr v-for="(document, idxDoc) in documents" :key="`doc-${idxDoc}`">
          <!-- Number of the document in the table. -->
          <td
            width="34"
            align="left"
            class="pr-0"
            style="text-align: left !important"
          >
            {{ idxDoc + progressionStep }}
          </td>
          <!-- Delete document. -->
          <td width="34" align="center" class="pr-0">
            <v-checkbox
              hide-details
              v-model="updateDocsToBeDelete"
              :value="idxDoc"
              color="error"
              class="mt-0 pt-0"
              @change="checkStatusListSelectedDocsDelete()"
            ></v-checkbox>
          </td>
          <!-- Other fields. -->
          <template v-for="(item, idxField) in fields">
            <td align="center" :key="idxField" :width="idxField ? '' : '100'">
              <span v-if="idxField" v-html="document[item.field]"></span>
              <template v-else>
                <!-- Link to document. -->
                <table width="100%">
                  <tr>
                    <td width="40">
                      <!-- Copy doc name to clipboard. -->
                      <v-tooltip top>
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn
                            fab
                            x-small
                            depressed
                            color="btnPrimary"
                            v-bind="attrs"
                            v-on="on"
                            @click="toClipboard(document[item.field])"
                            class="mr-2"
                          >
                            <v-icon color="white">mdi-content-copy</v-icon>
                          </v-btn>
                        </template>
                        <span>{{ document[item.field] }}</span>
                      </v-tooltip>
                    </td>
                    <td align="left" width="100">
                      <!-- Link to document form. -->
                      <v-btn
                        small
                        block
                        rounded
                        depressed
                        color="btnPrimary"
                        class="btn-doc-name"
                        :to="getDocFormUrl(idxDoc)"
                      >
                        <v-icon color="white" left>mdi-open-in-new</v-icon>
                        <span class="white--text">{{
                          document[item.field] | truncate(30)
                        }}</span>
                      </v-btn>
                    </td>
                  </tr>
                </table>
              </template>
            </td>
          </template>
          <!-- Date fields. -->
          <td width="180" v-html="formattingDate(document.created_at)"></td>
          <td width="180" v-html="formattingDate(document.updated_at)"></td>
          <!-- Hash field. -->
          <td width="40" align="center">
            <!-- Copy document ID to clipboard. -->
            <v-tooltip top>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  fab
                  x-small
                  depressed
                  color="btnPrimary"
                  v-bind="attrs"
                  v-on="on"
                  @click="toClipboard(document.hash)"
                >
                  <v-icon color="white">mdi-content-copy</v-icon>
                </v-btn>
              </template>
              <span>{{ document.hash }}</span>
            </v-tooltip>
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";

export default {
  name: "DocTable",

  computed: {
    ...mapState(["serviceList", "indexService", "indexCollection"]),
    ...mapState("documentList", [
      "documents",
      "deleteAllDocsFlag",
      "docsToBeDelete",
      "progressionStep",
      "docUrlWithoutID",
    ]),
    updateDeleteAllDocsFlag: {
      get: function () {
        return this.deleteAllDocsFlag;
      },
      set: function (flag) {
        this.setDeleteAllDocsFlag(flag);
      },
    },
    updateDocsToBeDelete: {
      get: function () {
        return this.docsToBeDelete;
      },
      set: function (arr) {
        this.setDocsToBeDelete(arr);
      },
    },
    // Get field list.
    fields: function () {
      return this.serviceList[this.indexService].collections[
        this.indexCollection
      ].fields;
    },
  },

  methods: {
    ...mapMutations("documentList", [
      "setDeleteAllDocsFlag",
      "setDocsToBeDelete",
    ]),
    ...mapActions("popUpMsg", ["runShowMsg"]),
    // Mark all documents for deletion.
    markAllDocsForDeletion() {
      this.setDocsToBeDelete(
        this.deleteAllDocsFlag ? [...Array(this.documents.length).keys()] : []
      );
    },
    // Check the status of the list of selected documents to be delete.
    checkStatusListSelectedDocsDelete() {
      this.setDeleteAllDocsFlag(
        this.docsToBeDelete.length === this.documents.length
      );
    },
    // Copy document link name to clipboard.
    // Copy document ID to clipboard.
    toClipboard(clipText) {
      if (navigator.clipboard) {
        navigator.clipboard
          .writeText(clipText)
          .then(() => {
            // console.log("ok");
          })
          .catch((err) => {
            this.runShowMsg({ text: err, status: "error" });
          });
      } else {
        // Your browser does not support Clipboard.
        this.runShowMsg({
          text: `${this.$t("message.12")}<br/>${this.$t("message.63")}`,
          status: "error",
        });
      }
    },
    // Get Url address to document form.
    getDocFormUrl(indexDoc) {
      return `${this.docUrlWithoutID}/${indexDoc}`;
    },
    // Formatting date.
    formattingDate(date) {
      const local = new Date(date + "Z");
      const localDate = local.toLocaleDateString([this.$userLang, "en"]);
      const localTime = local.toLocaleTimeString().slice(0, 8);
      return `<span class="txtSuccess--text font-weight-bold">${localDate}</span> <span class="txtWarning--text font-weight-bold">${localTime}</span>`;
    },
  },
};
</script>

<style>
.v-data-table__wrapper > table > thead.document-list > tr > th {
  background: none !important;
  font-size: 0.9rem !important;
  text-align: center !important;
}
.v-data-table__wrapper > table > tbody.document-list > tr > td {
  text-align: center !important;
}
</style>
