<template>
  <v-container fluid v-if="serviceList.length > 0">
    <v-card class="mt-1">
      <v-toolbar dense flat>
        <!-- Go back Home page -->
        <v-btn icon @click="goBackHomePage">
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
      </v-toolbar>
      <!-- Show collection name -->
      <v-card-title>{{ collectionTitle }}</v-card-title>
      <!-- Show breadcrumbs -->
      <v-card-subtitle>{{ breadcrumbs }}</v-card-subtitle>
      <v-card-text class="pt-4">
        <v-row>
          <v-col cols="12" md="6">
            <!-- Search query field. -->
            <SearchQuery :getDocumentList="getDocumentList" />
          </v-col>
          <v-col cols="12" md="6" class="text-md-right">
            <!-- Add a new document. -->
            <v-btn
              rounded
              depressed
              color="btnPrimary"
              class="white--text"
              :to="docUrlWithoutID + '/new'"
            >
              <v-icon color="white" left>mdi-file-plus-outline</v-icon>
              {{ $t("message.25") }}
            </v-btn>
          </v-col>
        </v-row>
        <v-row class="mb-4 mt-12">
          <v-col cols="auto" class="py-0">
            <!-- Sorting the list of documents. -->
            <div class="w-260">
              <v-select
                dense
                hide-details
                item-color="primaryTwo"
                v-model="updateSortDocList"
                :items="itemsSortDocList()"
                @change="getDocumentList()"
              ></v-select>
            </div>
          </v-col>
          <v-col cols="auto" class="py-0">
            <!-- Sorting direction. -->
            <div class="w-180">
              <v-select
                dense
                hide-details
                item-color="primaryTwo"
                v-model="updateSortDirectDocList"
                :items="itemsSortDirectDocList()"
                @change="getDocumentList()"
              ></v-select>
            </div>
          </v-col>
          <v-col cols="auto" class="py-0">
            <!-- Filter-2 for selective fields. -->
            <FilterTwo :getDocumentList="getDocumentList" />
          </v-col>
          <v-col cols="auto" class="py-0">
            <!-- Delete all selected documents. -->
            <v-btn
              small
              rounded
              depressed
              v-if="docsToBeDelete.length > 0"
              color="btnRed"
              @click="deleteDocs()"
            >
              <v-icon color="white" left>mdi-delete</v-icon>
              <span>{{ $t("message.4") }}</span>
            </v-btn>
          </v-col>
        </v-row>
        <!-- Document table. -->
        <DocTable />
      </v-card-text>
      <v-card-actions>
        <!-- Documents per page. -->
        <!-- Pagination. -->
        <PerPagePagination :getDocumentList="getDocumentList" />
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
import SearchQuery from "@/components/doc_list/SearchQuery.vue";
import FilterTwo from "@/components/doc_list/FilterTwo.vue";
import DocTable from "@/components/doc_list/DocTable.vue";
import PerPagePagination from "@/components/doc_list/PerPagePagination.vue";

export default {
  name: "DocumentList",

  components: {
    SearchQuery,
    FilterTwo,
    DocTable,
    PerPagePagination,
  },

  data: () => ({
    modelKey: "",
    serviceTitle: "",
    collectionTitle: "",
    fieldsName: [],
  }),

  computed: {
    ...mapState(["serviceList", "indexService", "indexCollection"]),
    ...mapState("documentList", [
      "documents",
      "sortDocList",
      "sortDirectDocList",
      "sortTypes",
      "docsToBeDelete",
      "docUrlWithoutID",
    ]),
    updateSortDocList: {
      get: function () {
        return this.sortDocList;
      },
      set: function (sort) {
        this.setSortDocList(sort);
      },
    },
    updateSortDirectDocList: {
      get: function () {
        return this.sortDirectDocList;
      },
      set: function (direc) {
        this.setSortDirectDocList(direc);
      },
    },
    // Get the route scheme to the collection.
    breadcrumbs: function () {
      return `${this.serviceTitle} > ${this.collectionTitle}`;
    },
  },

  methods: {
    ...mapMutations([
      "setService",
      "setIndexService",
      "setCollection",
      "setIndexCollection",
    ]),
    ...mapMutations("documentList", [
      "setSortDocList",
      "setSortDirectDocList",
      "setDeleteAllDocsFlag",
      "setDocsToBeDelete",
      "setDocUrlWithoutID",
    ]),
    ...mapMutations("popUpMsg", ["setShowMsg"]),
    ...mapActions("navigationBar", ["resetStateMenu"]),
    ...mapActions("documentList", [
      "ajaxGetDocumentList",
      "ajaxGetFilters",
      "resetToDefault",
    ]),
    ...mapActions("popUpMsg", ["runShowMsg"]),
    ...mapActions("pageLockout", ["runShowOverlayPageLockout"]),
    // Router - Go back one step.
    goBackHomePage() {
      this.resetStateMenu();
      this.setShowMsg(false);
      this.$router.replace({ name: "home" });
    },
    // Get document url without hash.
    getUrlNoIndexDoc: function () {
      const service = this.serviceList[this.indexService];
      const collection = service.collections[this.indexCollection];
      this.modelKey = collection.model_key;
      this.fieldsName = collection.fields.map((item) => item.field);
      this.serviceTitle = service.service.title;
      this.collectionTitle = collection.title;
      this.setDocUrlWithoutID(
        `/${this.serviceTitle}/${this.indexService}/${this.collectionTitle}/${this.indexCollection}/document`
      );
    },
    // Sorting options for the list of documents.
    itemsSortDocList() {
      return [
        { text: this.$t("message.40"), value: this.sortTypes[0] },
        { text: this.$t("message.29"), value: this.sortTypes[1] },
        { text: this.$t("message.30"), value: this.sortTypes[2] },
      ];
    },
    // Sort direction options.
    itemsSortDirectDocList() {
      return [
        { text: this.$t("message.42"), value: -1 },
        { text: this.$t("message.43"), value: 1 },
      ];
    },
    // Get a list of documents.
    getDocumentList() {
      this.setShowMsg(false);
      this.runShowOverlayPageLockout(true);
      this.setDeleteAllDocsFlag(false);
      this.setDocsToBeDelete([]);
      return new Promise((resolve, reject) => {
        this.ajaxGetDocumentList({
          modelKey: this.modelKey,
          fieldsName: this.fieldsName,
        })
          .then(() => {
            resolve();
          })
          .catch((error) => {
            console.log(error);
            this.runShowMsg({ text: error, status: "error" });
            reject(error);
          })
          .finally(() => {
            this.runShowOverlayPageLockout(false);
          });
      });
    },
    // Delete selected documents.
    deleteDocs() {
      this.setShowMsg(false);
      this.runShowOverlayPageLockout(true);
      const docHashList = [];
      this.docsToBeDelete.forEach((idx) => {
        docHashList.push(this.documents[idx].hash);
      });
      const payload = {
        model_key: this.modelKey,
        doc_hash_list: JSON.stringify(docHashList),
      };
      this.axios
        .post("/admin/delete-doc-list", payload)
        .then((response) => {
          const data = response.data;
          if (!data.is_authenticated) {
            this.setIsAuthenticated(false);
          } else if (data.msg_err.length > 0) {
            console.log(data.msg_err);
            this.runShowMsg({ text: data.msg_err, status: "error" });
          } else {
            this.setShowMsg(false);
            this.getDocumentList();
          }
        })
        .catch((error) => {
          console.log(error);
          this.runShowMsg({ text: error, status: "error" });
        })
        .finally(() => {
          this.runShowOverlayPageLockout(false);
        });
    },
  },

  created() {
    // Get current parameters URL address.
    this.setService(this.$route.params.service);
    this.setIndexService(this.$route.params.indexService);
    this.setCollection(this.$route.params.collection);
    this.setIndexCollection(this.$route.params.indexCollection);
    // Reset the values of all default parameters.
    this.resetToDefault();
    // Get document url without hash.
    this.getUrlNoIndexDoc();
    // Get a list of documents.
    this.getDocumentList();
    // Get a data for Filters.
    this.ajaxGetFilters(this.modelKey).catch((error) => {
      console.log(error);
      this.runShowOverlayPageLockout(false);
      this.runShowMsg({ text: error, status: "error" });
    });
  },
};
</script>
